<template>
    <a href="#" @click="$router.go(-1)">back</a>
    <div class="box">
     <!--   <NotesContent/> -->
        <component :is="dynamicComponent"></component>
    </div>
</template>

<script>
//import NotesContent from '@/components/notes/ctfwriteup/{{ route.params.id }}.md';
//import NotesContent from '@/components/notes/ctfwriteup/HTB/Medium/Vault/Writeup.md';

export default {
    name: 'NotesView',
    components: {
       
    },
    data: () => ({
        dynamicComponent: null
    }),
    methods: {
        updateComponent(platform, difficulty, name){
            import(`@/components/notes/ctfwriteup/${platform}/${difficulty}/${name}/Writeup.md`).then(module => {
                this.dynamicComponent = module.default;
            })
        }
    },
    beforeRouteEnter(to, from, next){
        next(vm => vm.updateComponent(to.params.platform, to.params.difficulty, to.params.name));
    },
    beforeRouteUpdate(to, from, next){
        this.updateComponent(to.params.platform, to.params.difficulty, to.params.name);
        next();
    }
}



</script>